.country-app {
  font-family: 'Lato', sans-serif;
  min-height: 100%;
}

.light {
  background-color: white;
  color: black;
}

.dark {
  background-color: #3d3d3a;
  color: white;
}

.country-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  text-align: center;
}

@media (min-width: 400px) and (max-width: 768px) {
  .country-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    text-align: center;
  }
}


@media(max-width: 399px) {
  .country-grid {
    display: grid;
    grid-template-columns: 1fr;
    text-align: center;
  }
}

.modal-header {
  font-weight: bold;
  padding: 1rem;
  border-bottom: 1px solid grey;
}

span {
  padding: 0 1rem 0 0;
}

button {
  border: 1px solid orange;
  border-radius: 5px;
  background-color: orange;
  color: white;
  cursor: pointer;
  font-weight: bold;
}

#root {
  height: 100vh;
}